

.input_container{
    display: flex;
    flex-direction: column;
}
.input_container input{
    width: 95%;
    margin: 1.5rem auto;
    padding: 1rem;
    border-radius: 25px;
    font-size: 1rem;
    font-family: inherit;
    outline: 0;
    border: 0;
    background-color:#CCC8AA ;
}
.login_button{
    display: block;
    margin: 3rem auto 1rem auto;
    width: 55%;
    padding: 0.75rem 1rem;
    border-radius: 25px;
    outline: 0;
    font-size: 1rem;
    font-family: inherit;
    cursor: pointer;
    background-color: #CCC8AA;
    color: #2C3333;
    border: 2px solid transparent;
}
.login_button:hover{
    background-color: #222222;
    border: 2px solid #CCC8AA;
    color: white;
}
.link_container{
    display: flex;
    justify-content: center;
}
.small{
    color: rgb(218, 218, 218);
    text-align: center;
    font-size: 0.85rem;
    text-decoration: underline;
    margin-bottom: 5rem;
    cursor: pointer;
}
.icons{
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}
.icon{
    color: white;
    cursor: pointer;
}
.error_msg{
    color: red;
    padding-left:1rem ;
    font-size: 0.85rem;
    margin: 0;
}
@media only screen and (max-width:620px) {
    .input_container input{
        width: 100%;
    }
    .login_button{
        width: 100%;
    }
    .error_msg{
        padding-left: 0.2rem;
    }
    
    
}
@media only screen and (max-height:670px) {
    .subtitle{
        margin-bottom: 2rem;
    }
    .small{
        margin-bottom: 3rem;
    }

    
    
}